import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import React, { Component } from 'react';
import title from './assets/ThinVersionTransparent.png';
import qrcode from './assets/QRCode.png';
import loading from './assets/Ethereum.png'
import termsofservice from './assets/Terms of Service.pdf'
import './App.css';
import { Navbar,NavItem,Nav,Modal,Popover,Tooltip,OverlayTrigger,Button,NavDropdown,MenuItem} from 'react-bootstrap';
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
//require('../assets/images/favicon.ico')

var ourAddress="0x7dAdAed57CE5d31Bb906cABD53AFc58e39744827"
var time=60;
var totalPot= 0.00;
var entriesArray = [{"address":ourAddress,"amount":"10.00000"},{"address":ourAddress,"amount":"20.00000"}];
var previousWinner={"amount":"0.0", "address":"None","odds":"0.0"};
var load=true
var Circle = require('rc-progress').Circle;

class Header extends Component {
  render() {
    return (
      <header className="Header">
        <Navbar inverse navbar-toggler className="Nav">
          <Navbar.Header>
            <Navbar.Brand>
              <NavLink to="/"><img src={title} className="Title" alt="EthRumble" Link to='/'/></NavLink>
            </Navbar.Brand>
            <Navbar.Toggle />
            </Navbar.Header>
            <Navbar.Collapse>
              <Nav pullRight>
                  <NavItem eventKey={1}>
                  <NavLink to="/about">About</NavLink>
                  </NavItem>
                  <NavItem eventKey={2}>
                  <NavLink to="/about">Specifications</NavLink>
                  </NavItem>
                  <NavItem eventKey={3}>
                  <NavLink to="/faq">FAQ</NavLink>
                  </NavItem>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </header>
    );
  }
};
class Footer extends Component {
  render () {
    return (
      <footer className="Footer">
        <ul className="BotNav">
          <li><NavLink to="/">Home</NavLink></li>
          <li><NavLink to="/about">About</NavLink></li>
          <li><NavLink to="/termsofservice">Terms of Service</NavLink></li>
          <li>Privacy Policy</li>
          <li>Support</li>
        </ul>
        <span>EthRumble@2018</span>
      </footer>
    );
  }
};
class Timer extends Component {
  constructor(props) {
    super(props);
    this.state = { timeMil: null, timeSec : null, totalEth : totalPot, timeMin : null};
  }
  componentDidMount() {
    setInterval( () => {
      this.setState({
        timeMil : new Date().getMilliseconds(),
        timeSec : new Date().getSeconds(),
        timeMin : new Date().getMinutes(),
        totalEth : totalPot
      })
    },50)
  }

  render () {
    let timeFinal=60-(this.state.timeSec+this.state.timeMil/1000);
    let timeFinalDisp=Number(timeFinal).toFixed(2);
    if(this.state.timeMin%2===0) {
      return (
      <div className="Clock">
         <Circle className="PercentCirc"
         percent={timeFinal/60*100}
         strokeWidth={5}
         trailWidth={5}
         strokeColor= "#0066ff"
         trailColor="#99c2ff"
          />
        <span className="Amount">{this.state.totalEth.toFixed(5)}</span>
        <span className="Timer">{timeFinalDisp} Seconds</span>
        <span className="Address">{ourAddress}</span>
        <img src={qrcode} className="QR" alt="QR Code" />
       </div>

      );
    }
    else {
      return (
      <div className="Clock">
         <Circle className="PercentCirc"
         percent={timeFinal/60*100}
         strokeWidth={5}
         trailWidth={5}
         strokeColor= "#FF8C00"
         trailColor="#FFCC99"
          />
          <span className="WaitingText">Deposits confirming, please hold</span>
          <span className="Timer" id="AltTimer">{timeFinalDisp} Seconds</span>
          <span className="Address" id="Total">Total Pot: {this.state.totalEth.toFixed(5)}</span>
          <img src={loading} className="LoadingImg" alt="Ethereum Logo" />
       </div>
      );
    }
  }
};
class Winner extends Component {
  constructor(props) {
    super(props);
    this.state = {previousWinnerState : previousWinner };
  }

  componentDidMount(Entries) {
    setInterval( () => {
    this.setState({
        previousWinnerState:previousWinner
      })
    },500)
  }
  render() {
    return (
      <div className="Winner">
        <strong>Previous Winner: </strong>
        <span>{(this.state.previousWinnerState["amount"]*1).toFixed(5)} Eth with a {this.state.previousWinnerState["odds"]*100}% chance of winning</span> <br />
        <span>{this.state.previousWinnerState["address"]}</span>
      </div>
    );
  }
};
class Entries extends Component {
  constructor(props) {
    super(props);
    this.state = {entries : entriesArray};
  }
  componentDidMount() {
    setInterval( () => {
      fetch("/api").then(results => {
        return results.json();
      }).then(data => {

        totalPot=data["totalPot"];
        let list=data["entrants"];
        let new_list=[];
        for (var i=0;i<list.length;i++) {
          new_list.push({"address":list[i]["from"],"amount":list[i]["amount"].toFixed(5)});
        }
        previousWinner=data["previousWinner"];
        this.setState({entries: new_list })
      })
    },500)
  }
  render() {
    let entriesList = this.state.entries.map((entry) =>
    <tr>
      <td key={entry.address} ><div id="Address">{entry.address}</div></td>
      <td key={entry.amount} ><div id="Amount">{entry.amount}</div></td>
    </tr>);
    return (
      <div className="Entries">
      <table className>
        {entriesList}
      </table>
      </div>
    );
  }
};
class TermsOfService extends Component{
  render() {
    return [
      <div className="Body">
      <Header />
      <div className="Content">
       <object data={termsofservice}></object>
      </div>
      <Footer />
      </div>
    ];
  }
}
class FAQ extends Component{
  render() {
    return [
      <div className="Body">
      <Header />
      <div className="Content">
          <h1 id="Info_Title">Frequently Asked Questions</h1>
          <h2>Where can I purchase Ethereum?</h2>
          <p>Ethereum can be purchased directly through Coinbase.</p>
          <h2>How long does it take for the platform to receive my Ethereum deposits?</h2>
          <p>The network should accept and confirm your deposit within the 1 minute confirmation time.  Please do not send from exchanges as there will be additional withdrawal time that is not considered in out confirmation period.</p>
          <h2>My deposit never showed up?  What do I do?</h2>
          <p>If your deposit hasn’t shown up, wait a few minutes and see if you were entered into any other pots.  Network confirmations are not controlled by us rather by the Ethereum Blockchain Network, therefore we have no control when your deposits receive the necessary confirmations and are entered into the pot.  Additionally, please check to make sure your deposit was lower than the maximum Ethereum deposit of 10 Eths.</p>
          <h2>Is it safe to have my Ethereum address publicly displayed to everyone?</h2>
          <p>Yes, the Ethereum address used to represent your deposit in the pot will be your public Ethereum address.  This address is perfectly safe to share with anyone, anywhere.</p>
          <h2>I won!  How do I withdraw my winning?</h2>
          <p>If you are the winner of a round, your Ethereum winning will be automatically sent to you in the next 15 minutes to the same Ethereum Address that you deposited your Ethereum from.  Please do not use addresses from Ethereum exchanges to avoid any problems with this process.</p>
          <h2>What are the fees for each pot?</h2>
          <p>Fees are calculated at a constant rate of 5% each pot.  After the deposit time for a round ends, the 5% fee will be calculated and taken from the winnings.</p>
      </div>
      <Footer />
      </div>
    ];
  }
}
class About extends Component{
  render() {
    return [
      <div className="Body">
      <Header />
      <div className="Content">
          <h1 id="Info_Title">Welcome to EthRumble!</h1>
          <h2>Rules and Info:</h2>
          <p>EthRumble is your typical jackpot platform operated using Ethereum.  To enter, you need to have Ethereum stored on a wallet and simply scan and send Ethereum from your wallet to the QR Code in the middle of the jackpot.</p>
          <p>Each game will have one minute countdown timer as well as a max pot size of 10 Eths, whichever restrictive cap is reached first.  Once a cap is hit, deposits will close and a one minute confirmation round of all deposits will begin.  After the confirmation period is over, a winner will be chosen randomly.</p>
          <p>Anyone’s deposit that fails to receive three confirmations on the Ethereum Blockchain by the end of the one minute confirmation period will automatically be placed into the next round.</p>
          <p>Once the confirmations are completed, a winner will be randomly picked.  To calculate your percentage chance of winning, simply take your deposit amount and divide by the size of the pot.</p>
          <p>When the winner is picked, the site will take a 5% fee and distribute the winnings to the Ethereum address of the winning player.</p>
          <p>After a winner has been selected for a round, the next round will begin immediately.</p>
      </div>
      <Footer />
      </div>
    ];
  }
}
class Home extends Component{
  constructor(props, context) {
    super(props, context);

    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);

    this.state = {
      show: load
    };
    load=false
  }
  handleClose() {
    this.setState({ show: false });
  }
  handleShow() {
    this.setState({ show: true });
  }
render() {
    return [
      <div className="Body">
      <div>
        <Modal show={this.state.show} onHide={this.handleClose}>
          <Modal.Header>Beta</Modal.Header>
          <Modal.Body>
            <h4>I am at least 18 years old, not a resident of the United States, and have read and agreed to the terms and conditions</h4>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.handleClose}>I agree</Button>
          </Modal.Footer>
        </Modal>
      </div>
      <Header />
      <div className="Content">
        <Timer />
        <Entries />
        <Winner />
      </div>
      <Footer />
      </div>
    ];
  }
};
class App extends Component {
  render() {
    return [
    <Router>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/faq" component={FAQ} />
          <Route path="/termsofservice" component={TermsOfService} />
        </Switch>
    </Router>
    ];
  }
};
export default App;



